import React, { useState } from 'react';
import { useAppDispatch, useAppSelector } from '../hooks/redux';
import { setApiKey } from '../store/slices/authSlice';
import { addNotification } from '../store/slices/uiSlice';
import { useGetSystemStatusQuery } from '../store/api/radarrApi';
import { Button, Input, Card } from '../components/common';
import styles from './LoginPage.module.css';

export const LoginPage = () => {
  const dispatch = useAppDispatch();
  const authError = useAppSelector(state => state.auth.error);
  const [apiKey, setApiKeyInput] = useState('');
  const [isValidating, setIsValidating] = useState(false);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!apiKey.trim()) {
      dispatch(addNotification({
        type: 'error',
        title: 'Validation Error',
        message: 'Please enter an API key',
      }));
      return;
    }

    setIsValidating(true);
    
    // Set the API key temporarily to test the connection
    dispatch(setApiKey(apiKey));
    
    // The system status query will automatically run when the API key is set
    // We'll handle the result in the next component render
  };

  // Use the system status query to validate the API key
  const { error: statusError, isLoading: statusLoading } = useGetSystemStatusQuery(undefined, {
    skip: !apiKey,
  });

  // Handle API key validation result
  React.useEffect(() => {
    if (isValidating && !statusLoading) {
      setIsValidating(false);
      
      if (statusError) {
        dispatch(setApiKey('')); // Clear invalid API key
        dispatch(addNotification({
          type: 'error',
          title: 'Authentication Failed',
          message: 'Invalid API key. Please check your credentials.',
        }));
      } else {
        dispatch(addNotification({
          type: 'success',
          title: 'Login Successful',
          message: 'Welcome to Radarr!',
        }));
      }
    }
  }, [isValidating, statusLoading, statusError, dispatch]);

  return (
    <div className={styles.container}>
      <div className={styles.loginCard}>
        <Card title="Welcome to Radarr" size="spacious">
          <form onSubmit={handleSubmit} className={styles.form}>
            <div className={styles.description}>
              <p>Enter your API key to access the Radarr interface.</p>
              <p className={styles.helpText}>
                You can find your API key in the Radarr settings under General  Security.
              </p>
            </div>
            
            <Input
              label="API Key"
              type="password"
              value={apiKey}
              onChange={(e) => setApiKeyInput(e.target.value)}
              placeholder="Enter your API key"
              required
              error={!!authError}
              errorText={authError || undefined}
              disabled={isValidating}
            />
            
            <Button
              type="submit"
              fullWidth
              loading={isValidating}
              disabled={!apiKey.trim()}
            >
              {isValidating ? 'Validating...' : 'Sign In'}
            </Button>
          </form>
        </Card>
      </div>
    </div>
  );
};