import { useGetMoviesQuery } from '../store/api/radarrApi';
import { Card } from '../components/common';
import styles from './MoviesPage.module.css';

export const MoviesPage = () => {
  const { data: movies, isLoading, error } = useGetMoviesQuery();

  if (error) {
    return (
      <div className={styles.container}>
        <div className={styles.error}>
          <h1>Error Loading Movies</h1>
          <p>Unable to load movies. Please try again later.</p>
        </div>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <div className={styles.header}>
        <h1>Movies</h1>
        <p>Manage your movie collection</p>
      </div>

      {isLoading ? (
        <div className={styles.loading}>
          <p>Loading movies...</p>
        </div>
      ) : (
        <div className={styles.moviesGrid}>
          {movies?.map(movie => (
            <Card
              key={movie.id}
              title={movie.title}
              subtitle={`${movie.year}  ${movie.status}`}
              interactive
            >
              <div className={styles.movieInfo}>
                <p className={styles.overview}>
                  {movie.overview ? 
                    (movie.overview.length > 150 ? 
                      `${movie.overview.substring(0, 150)}...` : 
                      movie.overview
                    ) : 
                    'No overview available'
                  }
                </p>
                <div className={styles.movieMeta}>
                  <span className={`${styles.status} ${movie.hasFile ? styles.downloaded : styles.missing}`}>
                    {movie.hasFile ? 'Downloaded' : 'Missing'}
                  </span>
                  <span className={styles.monitored}>
                    {movie.monitored ? 'Monitored' : 'Unmonitored'}
                  </span>
                </div>
              </div>
            </Card>
          )) || []}
          
          {movies && movies.length === 0 && (
            <div className={styles.empty}>
              <h2>No Movies Found</h2>
              <p>Start by adding some movies to your collection.</p>
            </div>
          )}
        </div>
      )}
    </div>
  );
};