import { createSlice } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';

export interface AuthState {
  isAuthenticated: boolean;
  apiKey: string | null;
  error: string | null;
  isLoading: boolean;
}

const initialState: AuthState = {
  isAuthenticated: false,
  apiKey: localStorage.getItem('radarr_api_key') || null,
  error: null,
  isLoading: false,
};

// Set initial authentication state based on stored API key
if (initialState.apiKey) {
  initialState.isAuthenticated = true;
}

const authSlice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    loginStart: (state) => {
      state.isLoading = true;
      state.error = null;
    },
    loginSuccess: (state, action: PayloadAction<string>) => {
      state.isAuthenticated = true;
      state.apiKey = action.payload;
      state.error = null;
      state.isLoading = false;
      localStorage.setItem('radarr_api_key', action.payload);
    },
    loginFailure: (state, action: PayloadAction<string>) => {
      state.isAuthenticated = false;
      state.apiKey = null;
      state.error = action.payload;
      state.isLoading = false;
      localStorage.removeItem('radarr_api_key');
    },
    logout: (state) => {
      state.isAuthenticated = false;
      state.apiKey = null;
      state.error = null;
      state.isLoading = false;
      localStorage.removeItem('radarr_api_key');
    },
    clearError: (state) => {
      state.error = null;
    },
    setApiKey: (state, action: PayloadAction<string>) => {
      state.apiKey = action.payload;
      state.isAuthenticated = !!action.payload;
      if (action.payload) {
        localStorage.setItem('radarr_api_key', action.payload);
      } else {
        localStorage.removeItem('radarr_api_key');
      }
    },
  },
});

export const {
  loginStart,
  loginSuccess,
  loginFailure,
  logout,
  clearError,
  setApiKey,
} = authSlice.actions;

export default authSlice.reducer;