import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
import type { 
  Movie, 
  SystemStatus, 
  HealthCheck, 
  QualityProfile, 
  RootFolder,
  MovieSearchParams,
  AddMovieRequest
} from '../../types/api';
import type { RootState } from '../index';

// Base query with authentication
const baseQuery = fetchBaseQuery({
  baseUrl: '/api/v3/',
  prepareHeaders: (headers, { getState }) => {
    const token = (getState() as RootState).auth.apiKey;
    
    if (token) {
      headers.set('X-Api-Key', token);
    }
    
    headers.set('Content-Type', 'application/json');
    return headers;
  },
});

export const radarrApi = createApi({
  reducerPath: 'radarrApi',
  baseQuery,
  tagTypes: [
    'Movie', 
    'MovieFile', 
    'QualityProfile', 
    'RootFolder', 
    'SystemStatus', 
    'Health',
    'Collection'
  ],
  endpoints: (builder) => ({
    // System endpoints
    getSystemStatus: builder.query<SystemStatus, void>({
      query: () => 'system/status',
      providesTags: ['SystemStatus'],
    }),

    getHealth: builder.query<HealthCheck[], void>({
      query: () => 'health',
      providesTags: ['Health'],
    }),

    // Movie endpoints
    getMovies: builder.query<Movie[], MovieSearchParams | void>({
      query: (params) => ({
        url: 'movie',
        params: params ? {
          page: params.page || 1,
          pageSize: params.pageSize || 20,
          sortKey: params.sortKey || 'title',
          sortDirection: params.sortDirection || 'asc',
          term: params.term,
        } : {
          page: 1,
          pageSize: 20,
          sortKey: 'title',
          sortDirection: 'asc',
        },
      }),
      providesTags: (result) =>
        result
          ? [
              ...result.map(({ id }) => ({ type: 'Movie' as const, id })),
              { type: 'Movie', id: 'LIST' },
            ]
          : [{ type: 'Movie', id: 'LIST' }],
    }),

    getMovie: builder.query<Movie, number>({
      query: (id) => `movie/${id}`,
      providesTags: (_, __, id) => [{ type: 'Movie', id }],
    }),

    addMovie: builder.mutation<Movie, AddMovieRequest>({
      query: (movie) => ({
        url: 'movie',
        method: 'POST',
        body: movie,
      }),
      invalidatesTags: [{ type: 'Movie', id: 'LIST' }],
    }),

    updateMovie: builder.mutation<Movie, Partial<Movie> & { id: number }>({
      query: ({ id, ...patch }) => ({
        url: `movie/${id}`,
        method: 'PUT',
        body: patch,
      }),
      invalidatesTags: (_, __, { id }) => [
        { type: 'Movie', id },
        { type: 'Movie', id: 'LIST' },
      ],
    }),

    deleteMovie: builder.mutation<void, { id: number; deleteFiles?: boolean; addImportExclusion?: boolean }>({
      query: ({ id, deleteFiles = false, addImportExclusion = false }) => ({
        url: `movie/${id}`,
        method: 'DELETE',
        params: { deleteFiles, addImportExclusion },
      }),
      invalidatesTags: (_, __, { id }) => [
        { type: 'Movie', id },
        { type: 'Movie', id: 'LIST' },
      ],
    }),

    // Search for movies to add
    searchMovies: builder.query<Movie[], string>({
      query: (term) => ({
        url: 'movie/lookup',
        params: { term },
      }),
    }),

    // Quality Profile endpoints
    getQualityProfiles: builder.query<QualityProfile[], void>({
      query: () => 'qualityprofile',
      providesTags: [{ type: 'QualityProfile', id: 'LIST' }],
    }),

    getQualityProfile: builder.query<QualityProfile, number>({
      query: (id) => `qualityprofile/${id}`,
      providesTags: (_, __, id) => [{ type: 'QualityProfile', id }],
    }),

    // Root Folder endpoints
    getRootFolders: builder.query<RootFolder[], void>({
      query: () => 'rootfolder',
      providesTags: [{ type: 'RootFolder', id: 'LIST' }],
    }),

    // Movie refresh and search
    refreshMovie: builder.mutation<void, number>({
      query: (id) => ({
        url: `command`,
        method: 'POST',
        body: {
          name: 'RefreshMovie',
          movieId: id,
        },
      }),
      invalidatesTags: (_, __, id) => [{ type: 'Movie', id }],
    }),

    searchMovie: builder.mutation<void, number>({
      query: (id) => ({
        url: `command`,
        method: 'POST',
        body: {
          name: 'MoviesSearch',
          movieIds: [id],
        },
      }),
    }),

    // Bulk operations
    refreshAllMovies: builder.mutation<void, void>({
      query: () => ({
        url: `command`,
        method: 'POST',
        body: {
          name: 'RefreshMovie',
        },
      }),
      invalidatesTags: [{ type: 'Movie', id: 'LIST' }],
    }),

    // Calendar endpoint
    getCalendar: builder.query<Movie[], { start: string; end: string; unmonitored?: boolean }>({
      query: (params) => ({
        url: 'calendar',
        params,
      }),
      providesTags: [{ type: 'Movie', id: 'CALENDAR' }],
    }),

    // History endpoint
    getHistory: builder.query<any[], { page?: number; pageSize?: number; sortKey?: string; sortDirection?: string }>({
      query: (params = {}) => ({
        url: 'history',
        params: {
          page: params.page || 1,
          pageSize: params.pageSize || 20,
          sortKey: params.sortKey || 'date',
          sortDirection: params.sortDirection || 'desc',
        },
      }),
    }),

    // Queue endpoint
    getQueue: builder.query<any[], { page?: number; pageSize?: number }>({
      query: (params = {}) => ({
        url: 'queue',
        params: {
          page: params.page || 1,
          pageSize: params.pageSize || 20,
        },
      }),
    }),
  }),
});

export const {
  // System hooks
  useGetSystemStatusQuery,
  useGetHealthQuery,
  
  // Movie hooks
  useGetMoviesQuery,
  useGetMovieQuery,
  useAddMovieMutation,
  useUpdateMovieMutation,
  useDeleteMovieMutation,
  useSearchMoviesQuery,
  
  // Quality Profile hooks
  useGetQualityProfilesQuery,
  useGetQualityProfileQuery,
  
  // Root Folder hooks
  useGetRootFoldersQuery,
  
  // Movie operations hooks
  useRefreshMovieMutation,
  useSearchMovieMutation,
  useRefreshAllMoviesMutation,
  
  // Calendar hooks
  useGetCalendarQuery,
  
  // History and Queue hooks
  useGetHistoryQuery,
  useGetQueueQuery,
} = radarrApi;